import "./index.scss";
var app = document.querySelector("#app");
var pathImages = "./assets/";
var pathIcons = "./assets/icons/";
var pathSounds = "./assets/sounds/";
var soundList = [
    {
        background: "".concat(pathImages, "summer-bg.jpg"),
        icon: "".concat(pathIcons, "sun.svg"),
        sound: createAudio("".concat(pathSounds, "summer.mp3"))
    },
    {
        background: "".concat(pathImages, "rainy-bg.jpg"),
        icon: "".concat(pathIcons, "cloud-rain.svg"),
        sound: createAudio("".concat(pathSounds, "rain.mp3"))
    },
    {
        background: "".concat(pathImages, "winter-bg.jpg"),
        icon: "".concat(pathIcons, "cloud-snow.svg"),
        sound: createAudio("".concat(pathSounds, "winter.mp3"))
    }
];
var currentSoundIndex = 0;
function createAudio(src) {
    var audio = document.createElement("audio");
    audio.src = src;
    audio.controls = true;
    return audio;
}
var selectSound = function (target) {
    currentSoundIndex = Number(target.id.replace("sound_", ""));
    var soundIcons = document.querySelectorAll(".list-carts__item img");
    soundIcons.forEach(function (icon, index) { return icon.src = soundList[index].icon; });
    if (!soundList[currentSoundIndex].sound.paused)
        soundList[currentSoundIndex].sound.pause();
    else {
        soundList.forEach(function (_a) {
            var sound = _a.sound;
            sound.pause();
            sound.currentTime = 0.0;
        });
        soundList[currentSoundIndex].sound.play();
        soundIcons[currentSoundIndex].src = "".concat(pathIcons, "pause.svg");
    }
    var main = document.querySelector(".main");
    main.style.background = "url(".concat(soundList[currentSoundIndex].background, ") no-repeat center/cover");
};
var editVolume = function (_a) {
    var target = _a.target;
    soundList.forEach(function (_a) {
        var sound = _a.sound;
        return sound.volume = Number(target.value) / 100;
    });
};
var renderContent = function () {
    app.innerHTML = "\n    <div class=\"main\" style=\"background: url(".concat(soundList[currentSoundIndex].background, ") no-repeat center/cover;\">\n      <div class=\"main__content\">\n        <h1>Weather sounds</h1>\n        <ul class=\"list-carts\">\n          ").concat(soundList.map(function (sound, index) {
        return "<li class=\"list-carts__item\">\n              <button class=\"list-carts__button\" id=\"sound_".concat(index, "\" style=\"background: url(").concat(sound.background, ") no-repeat top left/cover\">\n                <img src=\"").concat(sound.icon, "\" alt=\"\">\n              </button>\n            </li>");
    }).join(''), "\n        </ul>\n        <div>\n          <label>\n            <input type=\"range\" class=\"sound-volume\">\n          </label>\n        </div>\n      </div>\n    </div>\n  ");
    var buttons = document.querySelectorAll(".list-carts__button");
    buttons.forEach(function (button) {
        button.addEventListener("click", function (_a) {
            var target = _a.target;
            return selectSound(target);
        });
    });
    var inputVolumeElem = document.querySelector(".sound-volume");
    inputVolumeElem.addEventListener("input", function (event) { return editVolume(event); });
    soundList[currentSoundIndex].sound.play();
};
renderContent();
